<template lang="pug">

.contacts
	.link(v-for="link in links")
		span
			a(:href="link.link") {{ link.name }}
			.copy(v-if="link.copy" @click="copy(link.copy)")
				img(src="/img/copy.svg")
	input.hidden-input

</template>

<script>
export default {
	data(){
		return {
			links: [
				{
					name: 'VK',
					link: '#'
				},
				{
					name: 'Email: kobenko.evgeny@gmail.com',
					link: 'mailto:kobenko.evgeny@gmail.com?subject=Заказ на bunnyonthemoon',
					copy: 'kobenko.evgeny@gmail.com'
				},
				{
					name: 'Telegram',
					link: '#'
				},
				{
					name: 'Skype: Green Bunny',
					link: '#',
					copy: 'Green Bunny'
				},
				{
					name: 'WhatsApp',
					link: '#'
				},
			]
		}
	},
	methods: {
		copy(text){
			this.input.value = text
			this.input.focus()
			this.input.select()
			document.execCommand("copy")
			this.input.blur()
		}
	},
	computed: {
		input(){
			return this.$el.querySelector('.hidden-input')
		}
	}
}
</script>

<style lang="stylus" scoped>

.contacts
	position relative
	z-index 2
	width 600px
	margin auto
	display flex
	flex-direction column
	justify-content center
	height 100%
	height 100vh
	.link
		font-size 28px
		position relative
		span
			transition transform .5s
			display inline-block
			&:hover
				transform scale(1.08)
		a
			display inline-block
		&:nth-child(2n+1)
			text-align left
		&:nth-child(2n)
			text-align right
		.copy
			position absolute
			width 25px
			height 20px
			top 6px
			left 100%
			margin-left 5px
			cursor pointer
			img
				width 100%
				height 100%
				position absolute
				left 0
				top 0
				opacity .5
				transition transform .3s, opacity .3s
			&::after
				content 'Скопировано'
				position absolute
				left 100%
				bottom 0
				margin-left 5px
				font-size 17px
				line-height 1
				opacity 0
				transition opacity .5s .4s
			&:hover
				img
					transform scale(1.1)
					opacity .7
			&:active
				img
					opacity 1
				&::after
					opacity 1
					transition opacity .3s
	.hidden-input
		position absolute
		left -200px
		top -200px
		opacity 0

@media screen and (max-width: 800px)
	.contacts
		width 500px
		.link
			margin-bottom 10px
			font-size 24px
			.copy
				margin-left 5px
				top 3px
@media screen and (max-width: 600px)
	.contacts
		width 300px
		.link
			font-size 18px
			margin-bottom 10px
			&:nth-child(2n+1)
				text-align center
			&:nth-child(2n)
				text-align center
			.copy
				position relative
				left auto
				display inline-block
				margin-left 5px
				top 2px
				&::after
					display none

</style>